import React, { Suspense } from 'react'
import { WorkspaceDashboardSkeleton } from '@/components/loading/loading-components'
import { getMyTasks } from '@/app/data/task/get-my-tasks'
import { getWorkspaceProjectsByWorkspaceId } from '@/app/data/project/get-workspace-projects'
import { userRequired } from '@/app/data/user/is-user-authenticated'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { ProjectAvatar } from '@/components/project/project-avatar'
import { ProfileAvatar } from '@/components/profile-avatar'
import { CheckSquare, FolderKanban, Users, Clock } from 'lucide-react'
import Link from 'next/link'

interface WorkspacePageProps {
  params: Promise<{ workspaceId: string }>
}

async function WorkspaceDashboardContent({ workspaceId }: { workspaceId: string }) {
  await userRequired();
  
  // Fetch data in parallel for better performance
  const [tasks, { projects, workspaceMembers }] = await Promise.all([
    getMyTasks(),
    getWorkspaceProjectsByWorkspaceId(workspaceId)
  ]);

  const recentTasks = tasks.slice(0, 5);
  const recentProjects = projects.slice(0, 3);

  return (
    <div className="p-6 space-y-6 animate-fade-in-up">
      {/* Welcome Header */}
      <div className="space-y-2">
        <h1 className="text-responsive-xl font-bold bg-gradient-to-r from-foreground to-foreground/70 bg-clip-text text-transparent">
          Welcome to your workspace
        </h1>
        <p className="text-muted-foreground text-responsive-base">
          Here's an overview of your projects and tasks
        </p>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <Card className="card-hover">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-primary/10 rounded-lg">
                <FolderKanban className="h-5 w-5 text-primary" />
              </div>
              <div>
                <p className="text-2xl font-bold">{projects.length}</p>
                <p className="text-sm text-muted-foreground">Active Projects</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="card-hover">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-blue-500/10 rounded-lg">
                <CheckSquare className="h-5 w-5 text-blue-500" />
              </div>
              <div>
                <p className="text-2xl font-bold">{tasks.length}</p>
                <p className="text-sm text-muted-foreground">My Tasks</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="card-hover">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-green-500/10 rounded-lg">
                <Users className="h-5 w-5 text-green-500" />
              </div>
              <div>
                <p className="text-2xl font-bold">{workspaceMembers.length}</p>
                <p className="text-sm text-muted-foreground">Team Members</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="card-hover">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-orange-500/10 rounded-lg">
                <Clock className="h-5 w-5 text-orange-500" />
              </div>
              <div>
                <p className="text-2xl font-bold">
                  {tasks.filter(t => t.status === 'IN_PROGRESS').length}
                </p>
                <p className="text-sm text-muted-foreground">In Progress</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Recent Activity */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Recent Projects */}
        <Card className="card-hover">
          <CardHeader>
            <div className="flex items-center justify-between">
              <CardTitle className="text-lg">Recent Projects</CardTitle>
              <Link 
                href={`/workspace/${workspaceId}/projects`}
                className="text-sm text-primary hover:underline"
              >
                View All
              </Link>
            </div>
          </CardHeader>
          <CardContent className="space-y-3">
            {recentProjects.length > 0 ? (
              recentProjects.map((project: any, index: number) => (
                <div key={project.id} className={`p-3 border border-border rounded-lg hover:bg-accent/50 transition-colors fade-in-up stagger-delay-${index + 1}`}>
                  <Link href={`/workspace/${workspaceId}/projects/${project.id}`}>
                    <div className="flex items-start gap-3">
                      <ProjectAvatar name={project.name} className="!h-10 !w-10" />
                      <div className="flex-1">
                        <h4 className="font-medium truncate">{project.name}</h4>
                        <p className="text-sm text-muted-foreground line-clamp-2">
                          {project.description || "No description available"}
                        </p>
                        <div className="flex items-center gap-2 mt-2">
                          <Badge variant="outline" className="text-xs">
                            {project._count?.tasks || 0} tasks
                          </Badge>
                        </div>
                      </div>
                    </div>
                  </Link>
                </div>
              ))
            ) : (
              <div className="text-center py-8 text-muted-foreground">
                <FolderKanban className="h-12 w-12 mx-auto mb-3 opacity-50" />
                <p>No projects yet</p>
                <p className="text-sm">Create your first project to get started</p>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Recent Tasks */}
        <Card className="card-hover">
          <CardHeader>
            <div className="flex items-center justify-between">
              <CardTitle className="text-lg">My Recent Tasks</CardTitle>
              <Link 
                href={`/workspace/${workspaceId}/my-tasks`}
                className="text-sm text-primary hover:underline"
              >
                View All
              </Link>
            </div>
          </CardHeader>
          <CardContent className="space-y-3">
            {recentTasks.length > 0 ? (
              recentTasks.map((task: any, index: number) => (
                <div key={task.id} className={`p-3 border border-border rounded-lg hover:bg-accent/50 transition-colors fade-in-up stagger-delay-${index + 1}`}>
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-3 flex-1">
                      <div className="h-2 w-2 bg-primary rounded-full" />
                      <div className="flex-1 min-w-0">
                        <p className="font-medium truncate">{task.title}</p>
                        <p className="text-sm text-muted-foreground truncate">
                          {task.project?.name}
                        </p>
                      </div>
                    </div>
                    <div className="flex items-center gap-2">
                      <Badge variant={task.priority} className="text-xs">
                        {task.priority}
                      </Badge>
                      <ProfileAvatar 
                        url={task.assignedTo?.image} 
                        name={task.assignedTo?.name} 
                        className="!h-6 !w-6" 
                      />
                    </div>
                  </div>
                </div>
              ))
            ) : (
              <div className="text-center py-8 text-muted-foreground">
                <CheckSquare className="h-12 w-12 mx-auto mb-3 opacity-50" />
                <p>No tasks assigned</p>
                <p className="text-sm">You're all caught up!</p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}

async function page({ params }: WorkspacePageProps) {
  const { workspaceId } = await params;

  return (
    <Suspense fallback={<WorkspaceDashboardSkeleton />}>
      <WorkspaceDashboardContent workspaceId={workspaceId} />
    </Suspense>
  );
}

export default page
